#navbar-container {
	background-color: white;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	box-shadow: 3px 3px 3px rgba(0,0,0,0.3);
}

#navbar-content {
	font-family: "Roboto", "Trebuchet MS", sans-serif;
	display: flex;
	justify-content: space-evenly;
	max-width: 65%;
	margin: auto;
}


#main-content {
	border-radius: 25px;
	border: 1px solid #E7E9EB;
	/* #A6D4FF*/
	background-color: #E8F4FF;
	padding: 10px 30px 30px 30px;
	min-height: calc(100vh - 100px);
	max-width: 65%;
	margin: auto;
	margin-top: 10px;
}

.home-page-header {
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-weight: bold;
	color: #222222;
	text-align: center;
}

.home-page-content {
	/*font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;*/
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-size: 16px;
	color: #555555;
}


.project-page-header {
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-weight: bold;
	color: #222222;
	text-align: center;
}

.project-header {
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-weight: bold;
	color: #222222;
}

.project-description {
	/*font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;*/
	font-family: "Titillium", "Trebuchet MS", sans-serif;
	font-size: 16px;
	color: #555555;
}

#project-buttons {
	display: flex;
	justify-content: space-evenly;
	padding: 0px 30px 0px 30px;
}

.project-button {
	border-radius: 5px;
	/* #EEEEEE rgb(118, 118, 118) */
	border: 1px solid #EEEEEE;
	font-family: "Roboto";
	font-weight: bold;
	font-size: 14px;
	/* #23CE6B #DA3E52 #82D173; #169873 #36FFC6 #00FF80 #1D7EB5 #68A65B #4BA638 #5ED147 */
	/* rgb(239, 239, 239) #41693A */
	background-color: #82D173;
	color: white;
	padding: 6px 10px;
	margin: 0px 5px 0px 5px;
	cursor: pointer;
}

.project-button:hover {
	background-color: #54874A;
	transition-duration: 0.2s;
}
